body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}
html {
  font-size: 16px;
  box-sizing: border-box;
}
*,
*::after,
*::before {
  box-sizing: inherit;
}
.header {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 36, 164, 0.992);
  display: flex;
  color: white;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
}
.contenedor {
  width: 100%;
}
.contenedor h2 {
  text-transform: uppercase;
  text-align: center;
  font-size: 2rem;
}
.contenedor-noticias {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.section-menu article {
  width: 25rem;
  margin: 0.5rem;
  display: flex;
  flex-direction: column;
}
.section-menu article img {
  max-width: 100%;
}
.section-menu article a {
  text-decoration: none;
  color: black;
  font-size: 1.3rem;
}

.section-menu article h3 {
  margin-bottom: 0;
}
